<template>
    <!-- App -->
    <div id="app" class="layout-dark theme-amber">

        <!-- Statusbar -->
        <f7-statusbar></f7-statusbar>

        <!-- Left Panel -->
        <f7-panel left cover layout="dark">
            <f7-view id="left-panel-view" navbar-through :dynamic-navbar="true">
                <f7-navbar v-if="$theme.ios" title="Menu" sliding></f7-navbar>
                <f7-pages>
                    <f7-page>
                        <f7-navbar v-if="$theme.material" title="Menu" sliding></f7-navbar>
                        <f7-block-title>Instellingen</f7-block-title>
                        <f7-list>
                            <f7-list-item link="/lol" title="Uitloggen"></f7-list-item>
                        </f7-list>
                    </f7-page>
                </f7-pages>
            </f7-view>
        </f7-panel>

        <!-- Main Views -->
        <f7-views>
            <f7-view id="main-view" navbar-through :dynamic-navbar="true" main>
                <!-- iOS Theme Navbar -->
                <f7-navbar v-if="$theme.ios">
                    <f7-nav-left>
                        <f7-link icon="icon-bars" open-panel="left"></f7-link>
                    </f7-nav-left>
                    <f7-nav-center sliding>Mijn Garage</f7-nav-center>
                    <f7-link>
                        <icon name="wrench" scale="1.5"></icon>
                    </f7-link>
                </f7-navbar>
                <!-- Pages -->
                <f7-pages>
                    <f7-page>
                        <!-- Material Theme Navbar -->
                        <f7-navbar v-if="$theme.material">
                            <f7-nav-left>
                                <f7-link  icon="icon-bars" open-panel="left"></f7-link>
                            </f7-nav-left>
                            <f7-nav-center sliding>Mijn Garage</f7-nav-center>
                            <f7-nav-right>
                                <f7-link href="/notificationOverview/">
                                    <f7-chip :text="notificationCount" bg="red" color="white"></f7-chip>
                                    <icon class="wrenchPadding" name="wrench" scale="1.5"></icon>
                                </f7-link>
                            </f7-nav-right>
                        </f7-navbar>
                        <!-- Page Content -->
                        <f7-block-title>Welkom bij de Mijn garage!</f7-block-title>
                        <f7-block inner>
                            <p>Welkom het in het prototype van de Mijn garage applicatie. Probeer de functie <i>Voertuig opzoeken met kenteken</i>
                                nu uit!</p>
                        </f7-block>
                        <f7-block-title>Kies een van de onderstaande opties</f7-block-title>
                        <f7-list>
                            <f7-list-item link="/searchVehicle/" title="Voertuig opzoeken met kenteken"></f7-list-item>
                            <f7-list-item link="/vehicleOverview/" title="Mijn voertuigen"></f7-list-item>
                        </f7-list>
                    </f7-page>
                </f7-pages>
            </f7-view>
        </f7-views>
    </div>
</template>

<script>


    export default {
        name: 'app',
        methods: {
            getNotificationCount(){
                this.$store.dispatch('getNotificationCount', this.$store.state.user.user_id);
            }
        },
        computed: {
            notificationCount(){
                return this.$store.state.notificationCount ? this.$store.state.notificationCount : "0"
            }
        },
        created()
        {
            this.getNotificationCount();
        }
    }


</script>

<style>
    .layout-dark .chip {
        background-color: #f44336;
    }

    .wrenchPadding {
        padding-left: 5px;
    }

</style>



