import Vue from 'vue';
import Vuex from 'vuex';

import * as mutations from './mutations';
import * as actions from './actions';

Vue.use(Vuex);

const state = {
    "user": {
        "user_id": 1,
        "first_name": null
    },
    "currentVehicle": {
        "id": null,
        "user_id": null,
        "brand": null,
        "model": null,
        "mileage": null,
        "expectedYearlyMileage": null,
        "new_worth": null,
        "year": null,
        "license_plate": null,
        "cylinders": null,
        "vehicle_category": null,
        "free_weight": null,
        "engine_displacement": null,
        "apk_date": null,
        "engine_power": null,
        "created_at": null,
        "updated_at": null
    },
    "notificationCount": 0,
    "notifications": [],
    // {
    //     "id": null,
    //     "user_id": null,
    //     "description": null,
    //     "numberplate": null,
    //     "created_at": null,
    //     "updated_at": null
    // },
    "currentVehicleMaintenanceLeads": [{
        "id": null,
        "vehicle_id": null,
        "description": null,
        "data_source": null,
        "date_registered": null,
    }],
    "vehiclesOfCurrentUser":[],
    "vehicleRetrievementIsLoading": false,
    "vehicleIsSaved": false,
    "maintenanceRetrievementIsLoading": false,
    "errors": {
        "vehicleNotFound": false
    },
    "applicationIsOffline": false
};

/**
 * creates a new store with its components
 */
export default new Vuex.Store({
    state,
    getters: null,    // getters,
    actions,    // actions,
    mutations    // mutations
})


