/**
 * Sets the state with a new car
 * @param {Object} state
 * @param {String} newCarJson
 */
import * as localforage from "localforage";
export const setCurrentCar = (state, newCarJson) => {
    state.currentVehicle = newCarJson;
};

/**
 * Sets the vehicleNotFound status to true
 * @param state
 */
export const setVehicleNotFoundErrorMessage = (state) => {
    state.errors.vehicleNotFound = true;
};

/**
 * Sets the vehicleNotFound status to false
 * @param state
 */
export const resetVehicleNotFoundErrorMessage = (state) => {
    state.errors.vehicleNotFound = false;
};
/**
 * Sets the retrieval status of the car to true
 * @param {Object} state
 */
export const setVehicleRetrievalStatus = (state) => {
    state.vehicleRetrievementIsLoading = true;
};

/**
 * Sets the retrieval status of the car to false
 * @param {Object} state
 */
export const resetVehicleRetrievalStatus = (state) => {
    state.vehicleRetrievementIsLoading = false;
};

/**
 * Sets the state with a new car
 * @param {Object} state
 * @param {String} newCarJson
 */
export const setCurrentMaintenanceLeads = (state, newMaintenanceJson) => {
    state.currentVehicleMaintenanceLeads = newMaintenanceJson;
};

/**
 *
 * @param state
 * @param notificationJson
 */
export const setNotifications = (state, notificationJson) => {
    state.notifications = notificationJson;
};

/**
 *
 * @param state
 * @param notificationCount
 */
export const setNotificationCounter = (state, notificationCount) => {
    state.notificationCount = notificationCount;
};

/**
 *
 * @param state
 */
export const setNotificationCounterToZero = (state) => {
    state.notificationCount = 0;
};

/**
 * Sets the retrieval status of the maintenancedata to true
 * @param {Object} state
 */
export const setMaintenanceRetrievalStatus = (state) => {
    state.maintenanceRetrievementIsLoading = true;
};

/**
 * Sets the retrieval status of the maintenancedata to false
 * @param {Object} state
 */
export const resetMaintenanceRetrievalStatus = (state) => {
    state.maintenanceRetrievementIsLoading = false;
};

/**
 * set the status of the saving of the vehecle t
 * @param state
 */
export const setVehicleSaveStatus = (state) => {
    state.vehicleIsSaved = true;
};

/**
 *
 * @param state
 */
export const resetVehicleSaveStatus = (state) => {
    state.vehicleIsSaved = false;
};

/**
 *
 * @param state
 */
export const setVehicleUserIdAfterSaving = (state) => {
    state.currentVehicle.user_id = state.user.user_id;
};

/**
 *
 * @param state
 */
export const setOfflineMode = (state) => {
    state.applicationIsOffline = true;
};
/**
 *
 * @param state
 */
export const setOnlineMode = (state) => {
    state.applicationIsOffline = false;
};

/**
 *
 * @param state
 * @param arrayOfUserVehicles
 */
export const setVehiclesOfUser = (state, arrayOfUserVehicles) => {
        localforage.setItem('userVehicles', arrayOfUserVehicles, function (err) {});
        state.vehiclesOfCurrentUser = arrayOfUserVehicles;
};

/**
 *
 * @param state
 */
export const setVehiclesOfUserFromLocalStorage = (state) => {

    localforage.getItem('userVehicles', function (err, value) {
        console.log(value);
        state.vehiclesOfCurrentUser = value;
    });

};



