<template>
    <f7-page>
        <f7-navbar title="Voertuig opslaan" back-link="Annuleren" sliding></f7-navbar>

        <f7-block>
            <f7-grid class="centerText">
                <f7-col width="5" tablet-width="25"></f7-col>
                <f7-col width="45" tablet-width="25">
                    {{capitalizeFirstLetter(currentVehicle.brand)}}
                </f7-col>
                <f7-col width="45" tablet-width="25">
                    {{currentVehicle.model.toLowerCase()}}
                </f7-col>
                <f7-col width="5" tablet-width="25"></f7-col>
                <f7-col width="100" tablet-width="100">
                    <i>
                        {{getEngineDisplacementInLiters(currentVehicle.engine_displacement)}}L
                        {{currentVehicle.fuel.toLowerCase()}},
                        {{convertKwToPk(currentVehicle.engine_power)}}pk,
                        kenteken {{currentVehicle.license_plate.toUpperCase()}}
                    </i>
                </f7-col>
            </f7-grid>
            <f7-list form>
                <f7-list-item>
                    <f7-label>Kilometerstand</f7-label>
                    <f7-input type="number" v-model="mileage" placeholder="(optioneel)"></f7-input>
                </f7-list-item>
                <f7-list-item>
                    <f7-label>Verwachte jaarlijkse kilometers</f7-label>
                    <div class="label">
                        <small>
                            Wij vragen u hoeveel kilometer u per jaar rijdt, zodat we een betere
                            inschatting kunnen maken van het benodigde onderhoud zodat u altijd
                            moet een goed onderhouden voertuig onderweg bent.
                        </small>
                    </div>
                    <f7-input type="number" v-model="expectedYearlyMileage" placeholder="(optioneel)"></f7-input>
                </f7-list-item>
            </f7-list>
            <f7-buttons v-if="!vehicleSavedStatus" theme="amber">
                <f7-button back>Annuleren</f7-button>
                <f7-button v-on:click="saveVehicle">Opslaan</f7-button>
            </f7-buttons>
            <f7-col v-if="vehicleSavedStatus" class="succesCode" width="100" tablet-width="100">
                Voertuig opgeslagen
            </f7-col>
            <f7-buttons v-if="vehicleSavedStatus" theme="amber">
                <f7-button back v-on:click="resetVehicleSaved">ga terug</f7-button>
            </f7-buttons>
        </f7-block>
    </f7-page>
</template>

<script>
    export default {
        name: 'AskForMileage',
        data () {
            return {
                mileage: '',
                expectedYearlyMileage: '',
                disableSearchButton: true
            }
        },
        methods: {
            resetVehicleSaved(){
                this.mileage = '';
                this.expectedYearlyMileage = '';
                this.$store.commit('resetVehicleSaveStatus');
            },
            capitalizeFirstLetter(string) {
                return string.charAt(0).toUpperCase() + string.toLowerCase().slice(1);
            },
            getEngineDisplacementInLiters(displacement){
                return (Math.round(displacement / 100) / 10).toFixed(1);
            },
            convertKwToPk(kw){
                return Math.round(kw * 1.361);
            },
            saveVehicle(){
                this.$store.dispatch(
                    'saveCar',
                    {
                        userId: this.$store.state.user.user_id,
                        licensePlate: this.$store.state.currentVehicle.license_plate,
                        mileage: this.mileage !== '' ? this.mileage : 0,
                        expectedYearlyMileage: this.expectedYearlyMileage !== '' ? this.expectedYearlyMileage : 0
                    }
                );
            }
        },
        computed: {
            currentVehicle(){
                return this.$store.state.currentVehicle
            },
            vehicleSavedStatus(){
                return this.$store.state.vehicleIsSaved;
            }

        }
    }
</script>



