<template>
    <f7-page>
        <f7-navbar title="Mijn Voertuigen" back-link="terug" sliding></f7-navbar>
        <f7-block>
            <f7-list v-if="showList">
                <f7-list-item checkbox name="my-checkbox" v-for="(vehicle, index) in vehicles"
                              :checked="checkIfChecked(vehicle.id)" v-on:click="vehicleSelected(vehicle.id)">
                    <f7-list-item v-on:click="showVehicleDetail(index, vehicle.id)" link="/vehicleDetail/">
                        <f7-grid class="vehicleOverviewGrid">
                            <f7-col width="5" tablet-width="25">
                            </f7-col>
                            <f7-col class="vehicleOverviewText" width="90" tablet-width="50">
                                {{capitalizeFirstLetter(vehicle.brand)}} {{vehicle.model.toLowerCase()}}
                            </f7-col>
                            <f7-col width="5" tablet-width="25"></f7-col>
                            <f7-col class="centerText" width="100" tablet-width="100">
                                <i>
                                    {{getEngineDisplacementInLiters(vehicle.engine_displacement)}}L
                                    {{vehicle.fuel.toLowerCase()}},
                                    {{convertKwToPk(vehicle.engine_power)}}pk,
                                    kenteken {{vehicle.license_plate.toUpperCase()}}
                                </i>
                            </f7-col>
                        </f7-grid>
                    </f7-list-item>
                </f7-list-item>
            </f7-list>
            <f7-button v-if="selectedVehicleIds.length > 0 && !isOffline" color="red" v-on:click="deleteVehicles">Verwijder geselecteerde voertuigen ({{selectedVehicleIds.length}})</f7-button>
            <f7-button v-if="selectedVehicleIds.length > 0 && isOffline" disabled color="red" >De app is offline, verwijderen niet mogelijk</f7-button>
        </f7-block>
    </f7-page>
</template>
<script>
    export default {
        name: 'httpTest',
        data () {
            return {
                selectedVehicleIds: []
            }
        },
        methods: {
            getVehiclesOfUser(){
                this.$store.dispatch('getVehiclesOfUser', this.$store.state.user.user_id);
            },
            capitalizeFirstLetter(string) {
                return string.charAt(0).toUpperCase() + string.toLowerCase().slice(1);
            },
            getEngineDisplacementInLiters(displacement){
                return (Math.round(displacement / 100) / 10).toFixed(1);
            },
            convertKwToPk(kw){
                return Math.round(kw * 1.361);
            },
            showVehicleDetail(index, id){
                this.vehicleSelected(id);
                this.$store.commit('setCurrentCar', this.vehicles[index]);
            },
            vehicleSelected(id){
                var isFound = false;
                for (var i = 0; i < this.selectedVehicleIds.length; i++) {
                    if (this.selectedVehicleIds[i] === id) {
                        this.selectedVehicleIds.splice(i, 1);
                        isFound = true;
                    }
                }
                if(!isFound){
                    this.selectedVehicleIds.push(id);
                }

            },
            checkIfChecked(id){
                for (var i = 0; i < this.selectedVehicleIds.length; i++) {
                    if (this.selectedVehicleIds[i] === id) {
                        return true;
                    }
                }
                return false;
            },
            deleteVehicles(){
                console.log(this.selectedVehicleIds);
                this.$store.dispatch('deleteVehicles', this.selectedVehicleIds);

                this.selectedVehicleIds = [];
            }
        },
        computed: {
            vehicles()
            {
                return this.$store.state.vehiclesOfCurrentUser
            }
            ,
            showList()
            {
                return this.$store.state.vehiclesOfCurrentUser.length > 0
            }
            ,
            isOffline()
            {
                return this.$store.state.applicationIsOffline
            }
        }
        ,
        created()
        {
            this.getVehiclesOfUser();
            console.log("create overview")
        }
    }
</script>

<style>
    .vehicleOverviewText {
        text-align: left;
    }

    .vehicleOverviewGrid {
        width: 100%;
    }
</style>




