<template>
    <f7-block>
        <f7-list>
            <f7-list-item title="Autogegevens" divider></f7-list-item>
            <f7-list-item title="Merk">{{currentVehicle.brand}}</f7-list-item>
            <f7-list-item title="Model">{{currentVehicle.model}}</f7-list-item>
            <f7-list-item title="Kenteken">{{currentVehicle.license_plate}}</f7-list-item>
            <f7-list-item title="Voertuigcategorie">{{currentVehicle.vehicle_category}}</f7-list-item>
            <f7-list-item title="Gewicht">{{currentVehicle.free_weight}}kg</f7-list-item>
            <f7-list-item title="Bouwjaar">{{currentVehicle.year}}</f7-list-item>
            <f7-list-item title="Motorgegevens" divider></f7-list-item>
            <f7-list-item title="Vermogen">{{enginePowerKW}}kw/{{enginePowerPK}}pk</f7-list-item>
            <f7-list-item title="Cilinders">{{currentVehicle.cylinders}}</f7-list-item>
            <f7-list-item title="Motorinhoud">{{currentVehicle.engine_displacement}}cc</f7-list-item>
        </f7-list>
    </f7-block>
</template>

<script>

    export default {
        name: 'VehicleDataDetail',
        computed: {
            currentVehicle(){
                return this.$store.state.currentVehicle
            },
            enginePowerKW(){
                return Math.round(this.$store.state.currentVehicle.engine_power);
            },
            enginePowerPK(){
                return Math.round(this.$store.state.currentVehicle.engine_power * 1.362);
            }
        }
    }
</script>



