import SearchVehicle from '../components/SearchVehicle'
import AskForMileage from '../components/AskForMileage'
import VehicleDetail from '../components/DetailComponents/VehicleDetail'
import VehicleOverview from '../components/VehicleOverview'
import Notifications from '../components/Notifcations'


export default [
  {
    path: '/searchVehicle',
    component: SearchVehicle
  },
  {
    path: '/vehicleDetail',
    component: VehicleDetail
  },
  {
    path: '/askForMileage',
    component: AskForMileage
  },
  {
    path: '/vehicleOverview',
    component: VehicleOverview
  },
  {
    path: '/notificationOverview',
    component: Notifications
  }
]



