<template>
    <f7-page>
        <f7-navbar title="Voertuig zoeken met kenteken" back-link="Back" sliding></f7-navbar>
        <f7-block inner>
            <p>Zoek uw voertuig met het kenteken, bekijk de info en sla hem op!</p>
            <f7-list form>
                <!-- Text Input -->
                <f7-grid>
                    <f7-col width="5" tablet-width="25"></f7-col>
                    <f7-col width="90" tablet-width="25">
                        <div class="countryLetter">
                            NL
                        </div>
                        <div class="numberplate">
                            <f7-input v-on:click="resetVehicleNotFoundErrorMessage" id="numberplateInput"
                                      v-model="licensePlate" type="text" placeholder="Kenteken"
                                      :onkeypress="checkIfLicensePlateIsCorrect()"
                                      :maxlength="getMaxNumberOfLicensePlateLength()"></f7-input>
                        </div>
                    </f7-col>
                    <f7-col width="5" tablet-width="25"></f7-col>
                </f7-grid>
                <f7-col v-if="vehicleStatusCode" class="errorCode" width="100" tablet-width="100">
                    Voertuig niet gevonden
                </f7-col>
            </f7-list>
            <f7-button :disabled="disableSearchButton" v-on:click="searchLicensePlate">Haal auto op </f7-button>
            <f7-list>
                <f7-list-item class="centerText" v-if="showBasicCarInfo" title="Gevonden voertuig"
                              divider></f7-list-item>

            </f7-list>
            <f7-grid v-if="showBasicCarInfo" class="centerText">
                <f7-col width="5" tablet-width="25"></f7-col>
                <f7-col width="45" tablet-width="25">
                    {{capitalizeFirstLetter(currentVehicle.brand)}}
                </f7-col>
                <f7-col width="45" tablet-width="25">
                    {{currentVehicle.model.toLowerCase()}}
                </f7-col>
                <f7-col width="5" tablet-width="25"></f7-col>
                <f7-col width="100" tablet-width="100">
                    <i>
                        {{getEngineDisplacementInLiters(currentVehicle.engine_displacement)}}L
                        {{currentVehicle.fuel.toLowerCase()}},
                        {{convertKwToPk(currentVehicle.engine_power)}}pk,
                        kenteken {{currentVehicle.license_plate.toUpperCase()}}
                    </i>
                </f7-col>
            </f7-grid>
            <div v-if="vehicleSearchStatus" class="spinner">
                <f7-preloader color="amber" size="50px"></f7-preloader>
            </div>
            <f7-buttons v-if="showBasicCarInfo" theme="amber">
                <f7-button v-if="vehicleIsSavedToCurrentUser" href="/askForMileage/">Voertuig opslaan</f7-button>
                <f7-button href="/vehicleDetail/">Voertuig detail</f7-button>
            </f7-buttons>
        </f7-block>
    </f7-page>
</template>
<script>
    export default {
        name: 'SearchVehicles',
        data () {
            return {
                licensePlate: '',
                mileage: '',
                expectedYearlyMileage: '',
                disableSearchButton: true
            }
        },
        methods: {
            searchLicensePlate(){
                this.$store.dispatch('getVehicleDataFromLicensePlate', this.licensePlate);
            },
            resetVehicleNotFoundErrorMessage(){
                this.$store.commit('resetVehicleNotFoundErrorMessage');
            },
            resetVehicleSaved(){
                this.mileage = '';
                this.expectedYearlyMileage = '';
                this.$store.commit('resetVehicleSaveStatus');
            },
            capitalizeFirstLetter(string) {
                return string.charAt(0).toUpperCase() + string.toLowerCase().slice(1);
            },
            getMaxNumberOfLicensePlateLength(){
                if (this.licensePlate.indexOf("-") > 0) {
                    return 8;
                } else {
                    return 6;
                }
            },
            getEngineDisplacementInLiters(displacement){
                return (Math.round(displacement / 100) / 10).toFixed(1);
            },
            convertKwToPk(kw){
                return Math.round(kw * 1.361);
            },
            saveVehicle(){
                this.$store.dispatch(
                    'saveCar',
                    {
                        userId: this.$store.state.user.user_id,
                        licensePlate: this.$store.state.currentVehicle.license_plate,
                        mileage: this.mileage !== '' ? this.mileage : 0,
                        expectedYearlyMileage: this.expectedYearlyMileage !== '' ? this.expectedYearlyMileage : 0
                    }
                );
            },
            checkIfLicensePlateIsCorrect(){
                if (this.licensePlate.indexOf("-") > 0 && this.licensePlate.length === 8){
                    this.disableSearchButton = false;
                } else if (this.licensePlate.indexOf("-") < 0 &&this.licensePlate.length === 6){
                    this.disableSearchButton = false;

                } else {
                    this.disableSearchButton = true;
                }
            }
        },
        computed: {
            currentVehicle(){
                return this.$store.state.currentVehicle
            },
            vehicleStatusCode(){
                return this.$store.state.errors.vehicleNotFound;
            },
            showBasicCarInfo(){
                return this.$store.state.currentVehicle.brand || this.$store.state.errors.vehicleNotFound && this.$store.state.currentVehicle.brand;
            },
            vehicleSearchStatus(){
                return this.$store.state.vehicleRetrievementIsLoading;
            },
            vehicleSavedStatus(){
                return this.$store.state.vehicleIsSaved;
            },
            vehicleIsSavedToCurrentUser(){
                return this.$store.state.currentVehicle.user_id !== this.$store.state.user.user_id
            }

        }
    }
</script>

<style>
    .countryLetter {
        color: white;
        background-color: #0000F9;
        width: 20%;
        height: 40px;
        Float: left;
        vertical-align: middle;
        line-height: 40px;
        text-align: center;
        padding-bottom: 5px;
        padding-top: 5px;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }

    .numberplate {
        color: black;
        background-color: #ECA909;
        width: 80%;
        float: right;
        text-align: center;
        padding-bottom: 5px;
        padding-top: 5px;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
    }

    table {
        width: 100%;
    }

    #numberplateInput {
        color: black;
        background-color: #ECA909;
        text-align: center;
        border: none;
        text-transform: uppercase;
    }

    .errorCode {
        color: red;
        text-align: center;
    }
    .succesCode {
        color: green;
        text-align: center;
    }

    .centerText {
        text-align: center;
    }

    .spinner {
        width: 100%;
        text-align: center;
    }

</style>



