<template>
    <f7-block>
        <f7-list>
            <f7-list-item title="Onderhoudsdata" divider></f7-list-item>
            <f7-list-item title="APK datum">{{APKDate}}</f7-list-item>
            <f7-list-item v-if="maintenanceRetrievementIsLoading">
                <div class="spinner">
                    <f7-preloader color="amber" size="50px"></f7-preloader>
                </div>
            </f7-list-item>
            <f7-list-item v-if="!maintenanceRetrievementIsLoading" v-for="maintenanceLead in maintenanceData">
                {{maintenanceLead.description}}.
                <small></small>
                <div class="item-after">
                    <span class="badge bg-amber">{{getPrettyDate(maintenanceLead.date_registered)}}</span>
                </div>
            </f7-list-item>
        </f7-list>
    </f7-block>
</template>

<script>

    export default {
        name: 'VehicleMaintenanceDataDetail',
        methods: {
            searchLicensePlate(){
                this.$store.dispatch('getVehicleMaintenanceDataFromLicensePlate', this.licensePlate);
            },
            getPrettyDate(dateString){
                return dateString.slice(0, 10);
            }
        },
        computed: {
            licensePlate(){
                return this.$store.state.currentVehicle.license_plate;
            },
            maintenanceData(){
                return this.$store.state.currentVehicleMaintenanceLeads;
            },
            APKDate(){
                return this.$store.state.currentVehicle.apk_date;
            },
            maintenanceRetrievementIsLoading(){
                return this.$store.state.maintenanceRetrievementIsLoading;
            }
        },
        created() {
            this.searchLicensePlate();
        }

    }
</script>



